@use 'variables' as v;

h1,
h2,
h3 {
    font-family: v.$fuentePrincipal;
    text-align: center;
    line-height: 1.5;
    margin: 0 0 5rem 0;
    text-transform: uppercase;
}

h1 {
    font-size: 4rem;
}


h2 {
    font-size: 3.5rem;
    color: v.$primario;
    line-height: 1.3;
    
    span{
        display: block;
        font-size: 1.9rem;
        color: v.$secundario;
    }

    &::after {
        content: '';
        display: block;
        width: 10rem;
        height: 10rem;
        margin: 0 auto;
        background-image: url( ../img/cafe.svg );
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
}


h3 {
    font-size: 3rem;
}