@use '../base/mixins' as m;
@use '../base/variables' as v;

.galeria-grid {
    @include m.grid(2, 2rem);

    @include m.desktop {
        @include m.grid( 4, 2rem );
    }

    .imagen {
        img {
            width: 30rem;
            height: 30rem;
            object-fit: cover;
        }


        @include m.desktop {

            &:nth-child(1) {
                grid-row: 1/3;
                
                img {
                    height: 62rem;
                }
            }

            &:nth-child(2) {
                grid-column: 2/4;

                img {
                    width: 62rem;
                }
            }

            &:nth-child(4) {
                grid-row: 2/4;

                img {
                    height: 62rem;
                }
            }

        }
    }
}