@use '../base/mixins' as m;
@use '../base/variables' as v;


.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;

    @include m.tablet {
        flex-direction: row;
        margin-top: 0;
        gap: 1rem;
    }

    a {
        color: v.$blanco;
        font-size: 1.8rem;
        text-transform: uppercase;
        margin-bottom: 1rem;
        font-weight: 700;
        transition: color .3s ease-in-out;

        @include m.tablet {
            margin-bottom: 0;
        }


        &:hover {
            color: v.$secundario;
        }

        &.activo {
            color: v.$secundario;
        }
    }

}