@use '../base/mixins' as m;
@use '../base/variables' as v;

.header {
    position: relative;
    padding: 5rem 0;
    background-repeat: no-repeat;
    background-size: cover;

    @include m.telefono {
        min-height: 60rem;
    }

    &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: v.$negro;
        opacity: .6;
    }

    .header-contenido {
        position: relative;
        h1 {
            color: v.$blanco;
            margin-top: 5rem;

            @include m.tablet {
                margin-top: 10rem;
            }
        }
    }
}

.header-inicio {
    background-image: url( ../img/header_inicio.jpg );
}