@use '../base/mixins' as m;
@use '../base/variables' as v;

.iconos {
    margin-top: 5rem;

    .listado-iconos {

        @include m.tablet {
            @include m.grid( 3, 3rem );
        }

        li {

            img {
                margin: 0 auto;
                width: 17rem;
            }

            p {
                font-family: v.$fuenteCursiva;
                font-size: 3.3rem;
                color: v.$primario;
            }
        }
    }

    
}