html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #EBE7DE;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 1.9;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

a {
  text-decoration: none;
  color: #000000;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3 {
  font-family: "Raleway", sans-serif;
  text-align: center;
  line-height: 1.5;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.5rem;
  color: #5E311E;
  line-height: 1.3;
}
h2 span {
  display: block;
  font-size: 1.9rem;
  color: #C39C63;
}
h2::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

h3 {
  font-size: 3rem;
}

.header {
  position: relative;
  padding: 5rem 0;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 480px) {
  .header {
    min-height: 60rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000000;
  opacity: 0.6;
}
.header .header-contenido {
  position: relative;
}
.header .header-contenido h1 {
  color: #FFFFFF;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .header-contenido h1 {
    margin-top: 10rem;
  }
}

.header-inicio {
  background-image: url(../img/header_inicio.jpg);
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .barra-logo {
  margin: 0 auto;
  width: 50%;
}
@media (min-width: 768px) {
  .barra .barra-logo {
    width: 20rem;
    margin: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    margin-top: 0;
    gap: 1rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  font-size: 1.8rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
  font-weight: 700;
  transition: color 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-bottom: 0;
  }
}
.navegacion a:hover {
  color: #C39C63;
}
.navegacion a.activo {
  color: #C39C63;
}

.contenido-principal {
  padding: 4rem 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.iconos {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .iconos .listado-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}
.iconos .listado-iconos li img {
  margin: 0 auto;
  width: 17rem;
}
.iconos .listado-iconos li p {
  font-family: "Bona Nova", serif;
  font-size: 3.3rem;
  color: #5E311E;
}

.galeria-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .galeria-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria-grid .imagen img {
  width: 30rem;
  height: 30rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .galeria-grid .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria-grid .imagen:nth-child(1) img {
    height: 62rem;
  }
  .galeria-grid .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria-grid .imagen:nth-child(2) img {
    width: 62rem;
  }
  .galeria-grid .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria-grid .imagen:nth-child(4) img {
    height: 62rem;
  }
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5E311E;
}

.bg-blanco {
  background-color: #FFFFFF;
}

.mt-negativo-10 {
  margin-top: -10rem;
}

.pb-0 {
  padding-bottom: 0;
}